import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Lost from '../views/Lost.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/oops',
    name: 'Lost',
    component: Lost
  },
  {
    path: '/:catchAll(.*)',
    redirect: '/oops'
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
