        <div style="padding: 1vh 1vh 1vw 1vw; background-color:goldenrod; color:black;"><span>{{ details.short }}</span></div>
        <div style="padding: 1vh 1vh 1vw 1vw; background-color:black; color:goldenrod; text-align:justify; display: flex; flex-wrap:wrap; flex-direction:row; justify-content:space-around;"><div style="padding: 0 0 1vw 1vw;" v-for="(filter, index) in filters" :key="index">{{ filter + ": " + details[filter[0].toLowerCase() + filter.substring(1)] + " " }}</div></div>
        <div style="padding: 1vh 1vh 1vw 1vw; background-color:goldenrod; color:black;"><a :href="'https://www.imdb.com/title/tt' + details._id">Go to IMDb?</a></div>
      </div>
    </div>
    <div id="details" v-show="details" style="height:auto; width:auto; left:50%; top:30%; transform:translate(-50%); position:fixed; height: calc(1.48 * 18.5vw); z-index:8;">
      <button style="position:absolute; right:0; top:0; background-color:transparent; height:12.5%; padding-right:1vw; border: none;" class="popping" @click="clearDetails">‚ùå</button>
      <div style="display:flex; flex-direction:row; justify-content:space-around; height:100%;">
        <div style="height:100%;">
          <img :src="details.image" alt="image" style="height:100%; width:auto;">
        </div>
        <div style="height:100%;">
          <table style="height:100%; width:auto;">
            <tr style="height:10%;">
              <th><span>Title: </span></th>
              <td colspan="5"><span>{{ details.title }}</span></td>
            </tr>
            <tr style="height:10%;">
              <th><span>Genre: </span></th>
              <td style="" colspan="5"><span v-for="(genre, index) in details.genre" :key="index">{{ genre[0].toUpperCase() + genre.substring(1) + " " }}</span></td>
            </tr>
            <tr style="height:60%; text-align:justify;">
              <th><span>Short: </span></th>
              <td colspan="5"><span style="overflow:auto;">{{ details.short }}</span></td>
            </tr>
            <tr style="height:10%;">
              <th><span>Guide: </span></th>
              <td style="" colspan="1" v-for="(filter, index) in filters" :key="index"><span style="white-space:nowrap;">{{ filter + ": " + details[filter[0].toLowerCase() + filter.substring(1)] }}</span></td>
            </tr>
            <tr style="height:10%;">
              <th><span>More: </span></th>
              <td colspan="5"><a :href="'https://www.imdb.com/title/tt' + details._id">Go to IMDb?</a></td>
            </tr>
          </table>
        </div>
      </div>
    </div>
    <div id="paper"></div>
    <div id="gap"></div>
    <div id="nav">
      <input id="search" v-model="toSearch" @keyup.enter="search" placeholder="use space to separate words" :disabled="loading" />
      <button id="menu" :class="menu" @click="switchMenu">menu</button>
    </div>
    <div class="expandable" :class="menu">
      <div class="column">
        <div class="title">INCLUDE</div>
        <button class="toggle" v-for="(item, index) in tags" :key="index" :class="typeof item === 'string' && this.toggled.includes(item.toLocaleLowerCase()) ? 'positive' : 'passive'" @click="this.toggle('include', $event)">{{ item[0].toUpperCase() + item.substring(1) }}</button>
      </div>
      <div class="column">
        <div class="title">EXCLUDE</div>
        <button class="toggle" v-for="(item, index) in tags" :key="index" :class="typeof item === 'string' && this.toggled.includes('!' + item.toLocaleLowerCase()) ? 'negative' : 'passive'" @click="this.toggle('exclude', $event)">{{ item[0].toUpperCase() + item.substring(1) }}</button>
      </div>
      <div class="column">
        <div class="title">FILTERS</div>
        <button class="toggle" v-for="(item, index) in filters" :key="index" :class="typeof item === 'string' && this.toggled.includes(item.toLocaleLowerCase()) ? 'enabled' : 'passive'" @click="this.toggle('filters', $event)">{{ item[0].toUpperCase() + item.substring(1) }}</button>
        <div style="min-height:4.7vh;"></div>
        <div class="title">TYPE</div>
        <button class="toggle" :class="this.toggled.includes('switched') ? 'switched' : 'passive'" @click="this.toggle('type', $event)">üé¨‚ûîüïπÔ∏è</button>
      </div>
    </div>
    <div id="inventory">
      <div class="item" v-for="(item, index) in inventory" :key="index" @click="setDetails(item)">
        <img class="image" :src="item.image" :alt="item.title" />
      </div>
    </div>
    <div class="paginationContainer" v-show="count">
      <button v-show="this.currentPage() !== 1" class="paginationElement" @click="this.goToPage(this.currentPage() - 1)">Prev</button>
      <button v-show="!this.pages.includes(1)" class="paginationElement" @click="this.goToPage(1)">{{ 1 }}</button>
      <div v-show="!this.pages.includes(1)" class="paginationGap">...</div>
      <button v-show="this.pages.length > 1" :class="this.currentPage() === page ? 'paginationCurrent' : 'paginationElement'" @click="this.goToPage(page)" v-for="(page, index) in this.pages" :key="index">{{ page }}</button>
      <div v-show="!this.pages.includes(this.lastPage())" class="paginationGap">...</div>
      <button v-show="!this.pages.includes(this.lastPage())" class="paginationElement" @click="this.goToPage(this.lastPage())">{{ this.lastPage() }}</button>
      <button v-show="this.currentPage() !== this.lastPage()" class="paginationElement" @click="this.goToPage(this.currentPage() + 1)">Next</button>
    </div>
    <router-view />
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "App",
  components: {},
  data() {
    return {
      details: false,
      toggled: [],
      //tags: [ "Action", "Comedy", "Family", "History", "Mystery", "Sci-Fi", "War", "Adventure", "Crime", "Fantasy", "Horror", "News", "Sport", "Western", "Animation", "Documentary", "Film-Noir", "Music", "Reality-TV", "Talk-Show", "Biography", "Drama", "Game-Show", "Musical", "Romance", "Thriller" ],
      tags: [],
      filters: ["Fear", "Nudity", "Violence", "Profanity", "Substance"],
      toSearch: "",
      menu: "passive",
      inventory: [],
      count: 0, //this gets set by the server
      skips: 0, //client is supposed to change only this
      limit: 0, //this gets set by the server
      range: 2, //this one is a constant
      pages: [],
      loading: false
    };
  },
  computed: {},
  methods: {
    goToPage(page) {
      const query = JSON.parse(JSON.stringify(this.$route.query));
      query["skip"] = (page - 1) * this.limit
      this.$router.push({ name: "Home", query: query })
    },
    resetPages() {
      this.pages.length = 0
      const currentPage = this.currentPage()
      const lastPage = this.lastPage()
      const atStart = 1 - (currentPage - this.range)
      const atEnd = (currentPage + this.range) - lastPage
      let toStart = 1
      if (atStart < 0) {
        toStart -= atStart
      }
      let toEnd = lastPage
      if (atEnd < 0) {
        toEnd += atEnd
      }
      if (atStart > 0 && atEnd < 0) {
        toEnd = Math.min(lastPage, toEnd + atStart)
      }
      if (atEnd > 0 && atStart < 0) {
        toStart = Math.max(1, toStart - atEnd)
      }
      for (let index = toStart; index <= toEnd; index++) {
        this.pages.push(index)
      }
    },
    lastPage() {
      return Math.ceil(this.count / this.limit)
    },
    currentPage() {
      return Math.floor(this.skips / this.limit) + 1
    },
    clearDetails() {
      this.details = false
    },
    setDetails(record) {
      this.details = record
    },
    switchMenu(event) {
      if (this.$route.path !== '/') {
        this.menu = "passive";
        return
      }
      if (this.menu === "active") {
        this.menu = "passive";
      } else {
        this.clearDetails()
        this.menu = "active";
      }
    },
    toggle(type, event) {
      if (this.loading) {
        return
      }
      try {
        const query = JSON.parse(JSON.stringify(this.$route.query));
        let inner = event.target.innerHTML.toLocaleLowerCase();
        this.skips = 0
        query["skip"] = 0
        if (type === "type") {
          if ("type" in query && query.type === "game") {
            query.type = "film"
          } else {
            query.type = "game"
          }
        } else if (type === "include") {
          if ("genre" in query && query.genre.includes(inner)) {
            query.genre = query.genre.replace("," + inner, "");
            query.genre = query.genre.replace(inner + ",", "");
            query.genre = query.genre.replace(inner, "");
            if (!query.genre.length) {
              delete query["genre"]
            }
          } else {
            if (!("genre" in query)) {
              query.genre = "";
            }
            if (query.genre.length > 0) {
              query.genre += ",";
            }
            query.genre += inner;
            if ("notgenre" in query) {
              query.notgenre = query.notgenre.replace("," + inner, "");
              query.notgenre = query.notgenre.replace(inner + ",", "");
              query.notgenre = query.notgenre.replace(inner, "");
              if (!query.notgenre.length) {
                delete query["notgenre"]
              }
            }
          }
        } else if (type === "exclude") {
          if ("notgenre" in query && query.notgenre.includes(inner)) {
            query.notgenre = query.notgenre.replace("," + inner, "");
            query.notgenre = query.notgenre.replace(inner + ",", "");
            query.notgenre = query.notgenre.replace(inner, "");
            if (!query.notgenre.length) {
              delete query["notgenre"]
            }
          } else {
            if (!("notgenre" in query)) {
              query.notgenre = "";
            }
            if (query.notgenre.length > 0) {
              query.notgenre += ",";
            }
            query.notgenre += inner;
            if ("genre" in query) {
              query.genre = query.genre.replace("," + inner, "");
              query.genre = query.genre.replace(inner + ",", "");
              query.genre = query.genre.replace(inner, "");
              if (!query.genre.length) {
                delete query["genre"]
              }
            }
          }
        } else {
          if (inner in query) {
            delete query[inner];
          } else {
            query[inner] = "0";
          }
        }
        //console.log(query)
        this.$router.push({ name: "Home", query: query })
      } catch (error) {
        //console.log(error);
        this.menu = "passive";
        this.inventory.length = 0
        this.count = 0
        this.pages.length = 0
        this.$router.push({ path: "/oops" });
      }
    },
    resetToggled() {
      this.toggled.length = 0;
      Object.keys(this.$route.query).forEach(key => {
        if (key === "type") {
          if (this.$route.query["type"] === "game") {
            this.toggled.push("switched")
          }
        }
        if (key === "genre") {
          const contents = this.$route.query["genre"].split(",");
          contents.forEach((genre) => {
            this.toggled.push(genre);
          })
        } else if (key === "notgenre") {
          const contents = this.$route.query["notgenre"].split(",");
          contents.forEach((genre) => {
            this.toggled.push("!" + genre);
          })
        } else if (key !== "search") {
          this.toggled.push(key)
        } else if (key === "search") {
          let toWrite = ""
          const contents = this.$route.query["search"].split(",")
          for (let index = contents.length - 1; index >= 0; index--) {
            toWrite += contents[index] + " "
          }
          if (toWrite.length) {
            toWrite = toWrite.substr(0, toWrite.length - 1)
          }
          this.toSearch = toWrite
        }
      })
    },
    search(event) {
      try {
        const tagsToSearch = this.toSearch.split(/\s|[^a-zA-Z0-9_]/);
        let stringToSearch = "";
        for (let index = tagsToSearch.length - 1; index >= 0; index--) {
          if (!tagsToSearch[index].length) {
            tagsToSearch.splice(index, 1);
          } else {
            if (stringToSearch.length) {
              stringToSearch += ",";
            }
            stringToSearch += tagsToSearch[index];
          }
        }
        //console.log(tagsToSearch)
        const query = JSON.parse(JSON.stringify(this.$route.query));
        // if (stringToSearch.length) {
        //   //console.log(stringToSearch)
        //   query["search"] = stringToSearch
        // }
        query["search"] = stringToSearch
        //console.log(query)
        this.$router.push({ name: "Home", query: query });
      } catch (error) {
        //console.log(error);
        this.menu = "passive";
        this.inventory.length = 0
        this.count = 0
        this.pages.length = 0
        this.$router.push({ path: "/oops" });
      }
    },
    async fetchFor(path, parameters) {
      //console.log(JSON.stringify(parameters));
      await axios.post(path, parameters)
        .then((response) => {
          //console.log(response.data)
          this.inventory = response.data["items"];
          this.count = response.data["count"]
          this.tags = response.data["tags"]
          //console.log(this.count)
          this.limit = response.data["limit"]
          if ("skip" in this.$route.query) {
            this.skips = parseInt(this.$route.query["skip"])
          } else {
            this.skips = 0
          }
          this.resetPages()
        })
        .catch((error) => {
          console.log(error);
          this.menu = "passive";
          this.inventory.length = 0
          this.count = 0
          this.pages.length = 0
          this.$router.push({ path: "/oops" });
        });
    }
  },
  watch: {
    $route(to, from) {
      //console.log("routing...")
      this.loading = true
      this.clearDetails()
      this.inventory.length = 0
      this.count = 0
      this.pages.length = 0
      this.resetToggled()
      const path = this.$route.path
      //console.log(path)
      if (path !== "/") {
        this.menu = "passive";
        this.loading = false
        return
      }
      let url = ""
      if (process.env.NODE_ENV === "development") {
        url = "http://localhost:8081"
      } else {
        url = "/"
      }
      this.fetchFor(url, this.$route.query).then(data => {this.loading = false}).catch(error => {this.menu = "passive"; this.loading = false})
    }
  }
};
</script>


